*{
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
h1{
  margin: 1rem 0;
}
button{
  color: hsl(0, 0%, 100%);
  margin: 1rem 0;
  background-color: hsl(234, 29%, 20%);
  border: none ;
  border-radius: 0.5rem;
  width: 100%;
  padding: 1rem;
  cursor: pointer;
  transition:0.3s ;
}
button:hover{
  box-shadow: #e75480 0px 10px 36px 0px, #e29233 0px 0px 0px 1px;
  transition:0.3s ;
  background-color: #e75480;
  background-image: linear-gradient(90deg, #e75480 0%, #e29233 100%);
}
input{
  width: 100%;
  border-radius: 0.5rem;
  padding: 1rem;
  border: 0.5px solid hsl(231, 7%, 60%);
}
input:focus{
  outline: none;
}

/* AppComponent */
.App{
  display: flex;
  justify-content: center;
}

/* formComponent */
.detail-text{
  max-width: 21rem;
  margin: 1rem auto;
}
.list-graph{
  margin: 1rem 0;
  display: flex;
}
.correct-img{
  padding-right: 1rem;
}
.code-red{
  background-color: hsl(4, 100%, 67%,0.1);
  border-color: hsl(4, 100%, 67%);
  color: hsl(4, 100%, 67%);
}
.small-alert-from{
  margin: 1rem 0;
  justify-content: space-between;
  display: flex;
}
.pass-state{
  margin-top: 1rem;
  max-width: 290px;
}
.dismiss-message{
  margin-top:18rem ;
}

@media only screen and (min-width: 1440px) {
  article{
    margin-top: 8rem;
    max-width: 1000px;
    border-radius: 15px;
    padding: 1rem;
    height: fit-content;
    background-color: hsl(0, 0%, 100%);
    display: flex;
    flex-direction: row-reverse;
  }
  .detail-text{
    padding: 2rem;
    max-width: 30rem;
    margin: 1rem auto;
  }
  .detail-text h1{
    margin-bottom: 2rem;
    font-size: 57px;
  }
  .small-alert-from{
    margin-top: 3rem;
  }
  .App{
    height: 100vh;
    background-color: hsl(235, 18%, 26%);
  }
  .pass-state{
    margin-top: 12rem;
    border-radius: 15px;
    max-width: 29rem;
    padding: 3rem;
    height: fit-content;
    background-color: hsl(0, 0%, 100%);
  }
  .pass-state h1{
    font-size: 45px;
  }
  .dismiss-message{
    margin: 2rem 0;
  }
}